import React from "react"

import Header from "./header";

import Footer from "./footer";

import Note from "./note";

import Notes from "../Notes";

function CreateNote(Notes){
    return (<Note
            title={Notes.title}
            content={Notes.content}
            ></Note>)
}

function App(){
    return (<div>
        <Header/>
        {Notes.map(CreateNote)}
        <Footer/>

    </div>)
}

export default App;